<template>
  <div class="password-wrapper">
    <div class="center_block">
      <Nav />
      <div class="content">
        <div class="title-wrapper">
          <span>账号安全</span>
        </div>
        <div class="body-wrapper">
          <div class="title">
            <span>修改手机号</span>
          </div>
          <el-form
            :model="ruleForm"
            status-icon
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="手机号" prop="phone">
              <el-input
                type="password"
                v-model="ruleForm.phone"
                autocomplete="off"
                placeholder="请输入您的手机号"
              ></el-input>
            </el-form-item>
            <el-form-item label="图形验证码" prop="graphicsCode">
              <div class="item-wrapper">
                <div class="el-input-wrapper">
                  <el-input
                    type="password"
                    v-model="ruleForm.graphicsCode"
                    autocomplete="off"
                    placeholder="输入图形验证码"
                  ></el-input>
                </div>
                <div class="right-wrapper">reyui</div>
              </div>
            </el-form-item>
            <el-form-item label="验证码" prop="code" class="sms_code-wrapper">
              <div class="item-wrapper">
                <div class="el-input-wrapper">
                  <el-input
                    type="password"
                    v-model="ruleForm.code"
                    autocomplete="off"
                    placeholder="请输入验证码"
                  ></el-input>
                </div>
                <button type="button" :class="!show?'send-button_s':'send-button'">
                  <span v-show="show" @click="getCode">发送验证码</span>
                  <span v-show="!show" class="count">{{count}}s后重新发送</span>
                </button>
              </div>
            </el-form-item>
            <el-form-item>
              <div class="item-wrapper">
                <el-button type="warning" @click="submitForm('ruleForm')">确定</el-button>
                <el-button @click="$router.push({path:'account',query:{name:'security'}})">返回</el-button>
              </div>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Nav from "@/components/Nav";
export default {
  components: {
    Nav
  },
  props: {},
  data() {
    return {
      show: true,
      count:'',
      ruleForm: {},
      rules: {}
    };
  },
  watch: {},
  computed: {},
  methods: {
      getCode(){}
  },
  created() {},
  mounted() {}
};
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/pc/index";
.password-wrapper {
  width: 100%;
  height: 100%;
  background-color: #fff;
  display: flex;
  .sms_code-wrapper {
    .send-button {
      background-color: #eeeeee;
      border-radius: 4px;
      width: 120px;
      height: 40px;
      margin: 0;
      padding: 0;
      border: 1px solid transparent; //自定义边框
      outline: none;
      color: #666666;
      overflow: hidden;
      cursor: pointer;
      @include center;
      &:active {
        opacity: 0.8;
      }
      &:hover {
        opacity: 0.8;
      }
    }
    span {
      display: block;
      width: 100%;
      height: 100%;
    }
    .send-button_s {
      background-color: #eeeeee;
      border-radius: 4px;
      width: 120px;
      height: 40px;
      margin: 0;
      padding: 0;
      border: 1px solid transparent; //自定义边框
      outline: none;
      color: #666666;
      overflow: hidden;
      @include center;
    }
  }
}
</style>


